import React, { useEffect, useState } from 'react';
import headerBg from './assets/hbg.png';
import './Header.css';
import defaultAvatar from './assets/logo192.png';
import CoinImg from './assets/coin.png';
import GemImg from './assets/gem.png';

declare global {
  interface Window {
    Telegram: any;
  }
}

interface HeaderProps {
  onOpenModal: (text: string) => void;
}

export default function Header({ onOpenModal }: HeaderProps) {
  const [userName, setUserName] = useState<string>('');
  const [userPhoto, setUserPhoto] = useState<string | null>(null);

  useEffect(() => {
    if (window.Telegram && window.Telegram.WebApp) {
      const tg = window.Telegram.WebApp;

      if (tg.initDataUnsafe?.user?.first_name) {
        setUserName(tg.initDataUnsafe.user.first_name);
        setUserPhoto(tg.initDataUnsafe.user.photo_url || null);
      }

      tg.ready();
    }
  }, []);

  return (
    <div className="header-content">
      <img src={headerBg} className="headerbg" alt="Header background"/>
      <img
        src={userPhoto || defaultAvatar}
        onClick={() => onOpenModal('')}
        alt="User avatar"
        className="user-avatar"
        onError={(e) => {
          (e.target as HTMLImageElement).src = defaultAvatar;
        }}
      />
      <h1 className="nickname">{userName}</h1>
      <div className='coins-row'>
        <img src={CoinImg} className="coin" alt="Coins" />
        <h1 className="CoinsQuantity">0</h1>
      </div>
      <div className='gems-row'>
        <img src={GemImg} className="gem" alt="Gems" />
        <h1 className="GemsQuantity">0</h1>
      </div>
    </div>
  );
}
