// src/Footer.tsx
import React from 'react';
import './Footer.css';
import footerBg from './assets/fbg.png';
import btn1 from './assets/tasks.png';
import btn2 from './assets/wallet.png';
import btn3 from './assets/friends.png';

export default function Footer({ onOpenModal }: { onOpenModal: (content: string) => void }) {
  return (
    <div className="footer-content">
      <img src={footerBg} className="footerbg" alt="footer background" />

      <div className="footer-buttons">
        <img src={btn1} alt="" className="footer-button-image" onClick={() => onOpenModal('')} />
        <img src={btn2} alt="" className="footer-button-image" onClick={() => onOpenModal('')} />
        <img src={btn3} alt="" className="footer-button-image" onClick={() => onOpenModal('')} />
      </div>
    </div>
  );
}
