import React, { useEffect, useState } from 'react';
import { TonConnectButton } from '@tonconnect/ui-react';
import { useTonConnectUI } from '@tonconnect/ui-react';
import axios from 'axios';

export default function WalletConnectPanel() {
  const [tonConnectUI] = useTonConnectUI();
  const [balance, setBalance] = useState<string | null>(null);

  const userAddress = tonConnectUI.account?.address;

  useEffect(() => {
    const fetchBalance = async () => {
      if (!userAddress) return;

      try {
        const response = await axios.get(`https://tonapi.io/v1/accounts/${userAddress}`);
        const tonBalance = response.data.balance;
        setBalance((+tonBalance / 1e9).toFixed(3)); // convert nanoton -> TON
      } catch (error) {
        console.error('  :', error);
        setBalance(null);
      }
    };

    fetchBalance();
  }, [userAddress]);

  return (
    <div className="wallet-container">
      <TonConnectButton />
      {userAddress && (
        <div className="wallet-balance">
          : {balance !== null ? `${balance} TON` : '...'}
        </div>
      )}
    </div>
  );
}
