// src/App.tsx
import React, { useState } from 'react';
import { TonConnectUIProvider } from '@tonconnect/ui-react';
import Header from "./Header";
import Footer from './Footer';
import './App.css';
import bgImage from './assets/bg.png';
import Modal from './Modal';
import TonConnectButton from './TonConnectButton';

function App() {
  const [isModalOpen, setModalOpen] = useState(false);
  const [modalContent, setModalContent] = useState('');

  const openModalWithContent = (content: string) => {
    setModalContent(content);
    setModalOpen(true);
  };

  const closeModal = () => setModalOpen(false);

  return (
    <TonConnectUIProvider manifestUrl={`${window.location.origin}/tonconnect-manifest.json`}>
      <div className="page-wrapper">
        <img src={bgImage} alt="Background" className="background-image" />

        <div className="interactive-area">
          <button className="map-button house-1" onClick={() => openModalWithContent('')}></button>
          <button className="map-button house-2" onClick={() => openModalWithContent('')}></button>
        </div>

          {isModalOpen && (
            <Modal onClose={closeModal}>
              <h2 className="modal-title">{modalContent}</h2>
              {modalContent === '' && (
                <div className="wallet-panel">
                  <TonConnectButton />
                </div>
              )}
            </Modal>
          )}

        <Header onOpenModal={openModalWithContent} />
        <Footer onOpenModal={openModalWithContent} />
      </div>
    </TonConnectUIProvider>
  );
}

export default App;
